.hamburger:hover span:nth-child(1)
{  width: 215px}

.mpost .post-img {
    position: absolute;
    top: 0;
    z-index: -1;
}
.mpost .content {
    background: white;
    padding: 10px 20px 10px 25px;
    text-align: justify;
    margin-top: 35%;
    margin-left: 8%;
}
.mpost .content h1 {
    font-size: 18px;
    text-align: left;
    text-transform: uppercase;
    font-weight: bold;
}
.mpost .content h1:before {
    content: "";
    position: absolute;
    background: red;
    height: 45px;
    width: 3px;
    left: 12%;
}
.mobile .pagination > li > a {
    font-size: 10px;
    padding: 1px 5px;
}
