.social {

   
    position: fixed;
    top:60%;
    left: 0;
}
.social div{
    margin-left: 5px;
    outline: none;
}
.mpost .post-img img{
    height: 200px;
}
.mobile .pagination {
    margin-bottom: 50px;
}
.dockicon {
    display: flex;
    justify-content: space-around;
    vertical-align: middle;
    align-items: center;
    align-content: center;
    height: 40px;
}


.mlogo {
    width: 100px;
    margin: 0 auto;
    margin-top: 12px;
}
.mlogo img{
    width: 100%;
}
button.share-btn.web-share {
    padding: 0;
    background: none;
    border: none;
    margin: unset;
    content: "";
    position: absolute;
    right: 30px;
    visibility: hidden;
}
.video div {
    background: #000;
}
