.header .navbar-default{
    background: transparent;
    border-radius: unset;
    border: none
}
.header .navbar-default .navbar-nav > .active > a{
    background-color: #000;
    color: #fff;
    
}
.header .navbar-default .navbar-nav > .active > a:hover, .header .navbar-default .navbar-nav > .active > a:focus{
    background-color: #000;
    border: none;
    outline: none;
    color: #fff
}