.bgcp {
    margin: unset;
    padding: unset;
}
.latest-news h3 {
    color: #00a950;
    
}
.homepage .post-image{
    overflow: hidden;
    height: 160px;
}
.homepage .post-image1{
    overflow: hidden;
    position: relative;
}
.homepage .post-image img, .homepage .post-image1 img {
    width: 100%;
    -webkit-filter: brightness(70%);
            filter: brightness(70%);
    -webkit-transition: -webkit-transform 2s, -webkit-filter 1.5s ease-in-out;
    transition: -webkit-transform 2s, -webkit-filter 1.5s ease-in-out;
    transition: transform 2s, filter 1.5s ease-in-out;
    transition: transform 2s, filter 1.5s ease-in-out, -webkit-transform 2s, -webkit-filter 1.5s ease-in-out;
    height:100%
}
.homepage .post-image img:hover, .homepage .post-image1 img:hover{
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    -webkit-filter: brightness(100%);
            filter: brightness(100%)
}

.homepage .ln-box1 {
   
  
    overflow: hidden;
}
.homepage .ln-box2{
    overflow: hidden;
    height: 400px;
}

.homepage .ln-box2 .post-image {
    height: 160px;
    overflow: hidden;
}
.ln-content {
    margin-bottom: 15px;
}
.ln-content p{
  
    font-size: 18px;
    color: #222;
    font-weight: 400;
}
.post-image{
    position: relative;
}
.ln-box1 a.category, .ln-box2 .post-image a{
    position: absolute;
    z-index: 9;
    background: black;
    bottom: 0;
    left: 0;
    text-decoration: none;
    color: #fff;
    text-align: left;
    padding: 1px 10px;
    font-size: 12px;
}
.ln-content .author, .ln-content .date{
    color: #b7b9c2;
    text-decoration: none;
    text-transform: uppercase;
    font-family: jost;
    font-size: 0.8125rem;
    font-weight: 500;
    letter-spacing: 0.125em;
    margin-right: 25px;
}
.ln-content a.category:hover{
    color: #000;
}
.ln-box1 .ln-content .title a{
    font-size: 16px;
}
.ln-content .title{
    font-size: 1.5rem;
    text-decoration: none; 
    line-height: 25px;
    margin-top: 10px;
    margin-bottom: 0px;
    height: 50px;
    text-overflow: ellipsis;
    overflow: hidden;
    -webkit-transition: 2s;
    transition: 2s;
}

.ln-content .title a{
    text-decoration: none; 
    color: #000;
    font-family: jost;
    letter-spacing: -0.025em;
    font-size: 16px;
}
.ln-content .title a:hover{
    color: #6c757d
}
.ln-content .excerpt{
    font-size: 15px;
    line-height: 1.5;
    margin-top: 1rem;
    font-family: 'Lato', sans-serif;
    text-align: justify;
    height: 90px;
    overflow: hidden;
}
.ln-content .readmore, .entry-details a{
    background: #f2f4f6;
    color: #000;
    padding-left: 1rem;
    padding-right: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 120px;
    padding: 7px;
    border-radius: 70px;
    text-decoration: none;
    -webkit-transition: cubic-bezier(0.6, 0.04, 0.98, 0.335);
    transition: cubic-bezier(0.6, 0.04, 0.98, 0.335);
    text-transform: uppercase;
    font-size: 11px;
    font-family: jost;
}
.ln-content .readmore:hover, .entry-details a:hover{
    color: #f2f4f6;
    background: #000;
}
.ln-content .readmore:after,  .entry-details a:after{
    display: inline-block;
    margin-left: 8px;
    font-family: FontAwesome;
    font-size: 8px;
    content: "\f054";
    margin-top: 2px;
}

